.login {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}  

.loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
}

.loginLeft,.loginRight {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.loginLogo {
    font-size: 50px;
    font-weight: 800;
    color: #4912c1;
    margin-bottom: 10px;
}

.loginDesc {
    font-size: 25px;
}

.loginBox{
    height: 450px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 10px ;
}

.loginInput{
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}

.loginInput:focus{
    outline: none;
}

.loginButton{
    height: 50px;
    width: 100%;
    border-radius: 20px;
    border: none;
    background-color: #4912c1;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    align-self: center;

}

.loginForgot{
    text-align: center;
    color: #4912c1;
}

.loginRegisterButton{
    width: 60%;
    align-self: center;
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #42b72a;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}