.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.rightbarProfileImgContainer{
    margin-right: 10px;
    position: relative;
}

.rightbarProfileImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

.rightbarOnline{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color:limegreen;
    position: absolute;
    top: -3px;
    right: 0;
    border: 2px solid white;
}

.rightbarUsername{
    font-weight: 500;
    font-size: 18px;

}