.topbarContainer{
    height: 50px;
    width: 100%;
    background-color: #4912c1;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.topbarLeft{
    flex: 2.5 1;
}

.logo{
    font-size: 24px;
    margin-left: 50px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.topbarCenter{
    flex: 5 1;
}

.searchbar{
    width: 100%;
    height: 30px;
    background-color: white;
    border-radius: 30px;
    display: flex;
    align-items: center;
}

.searchIcon{
    font-style: 20px !important;
    margin-left: 10px;
}

.searchInput{
    border: none;
    width: 70%;
}

.searchInput:focus{
    outline: none;
}

.topbarRight{
    flex: 3 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: white;
}

.topbarLink{
    margin-right: 10px;
    font-style: 14px;
    cursor: pointer;
}

.topbarIcons{
    display: flex;
}

.topbarIconItem{
    margin-right: 15px;
    cursor: pointer;
    position: relative;
    
}

.topbarIconBadge{
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    color: white;
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.topbarImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}
.sidebar {
    flex: 2 1;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
  }
  
  ::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgb(179, 179, 179);
  }
  
  .sidebarWrapper {
    padding: 20px;
  }
  
  .sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  .sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    margin-left: 10px;
    font-size: 19px;
  }
  
  .sidebarIcon {
    margin-right: 15px;
  }
  
  .sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5.5px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .sidebarHr {
    margin: 20px 0;
  }
  
  .sidebarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
  }


.sidebarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    margin-left: 10px;
    font-size: 19px;
}

.sidebarFriendImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.feed{
    flex: 5.5 1;
}

.feedWrapper{
    padding: 20px;
}
.post {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;
}
  
.postWrapper {
    padding: 15px;
}

.postTop {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
  
.postTopLeft {
    display: flex;
    align-items: center;
}
  
.postProfileImg {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    object-fit: cover;
}
  
.postUsername {
    font-size: 17px;
    font-weight: 500;
    margin: 0 15px;
}
  
.postDate{
    font-size: 15px;
    margin-left: 5px;
}

.postCenter{
    margin: 10px 0;
}

.postImg{
    margin-top: 15px;
    width: 100%;
    max-height: 450px;
    object-fit: contain;
}

.postBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postBottomLeft{
    display: flex;
    align-items: center;   
}

.likeIcon{
    width: 26px;
    height: 26px;
    margin-right: 10px;
    cursor: pointer;
}

.postLikeCounter{
    font-size: 17px;
}

.postCommentText{
    cursor: pointer;
    border-bottom: 1px dashed gray;
    font-size: 17px;
}
.share {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
}
  
.shareWrapper {
    padding: 10px;
}
  
.shareTop {
    display: flex;
    align-items: center;
}
  
.shareProfileImg {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.shareInput {
    border: none;
    width: 80%;
    padding: 10px;
    

}
  
.shareInput:focus {
    outline: none;
}
  
.shareHr {
    margin: 20px;
}
  
.shareBottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.shareOptions{
    display: flex;
    margin-left: 25px;
}

.shareOption{
    display: flex;
    align-items: center;
    margin-right: 20px;
    cursor: pointer;
}

.shareIcon{
    font-size: 19px;
    margin-right: 5px;
}

.shareOptionText{
    font-size: 18px;
    font-weight: 500;
}

.shareButton{
    border: none;
    padding: 15px;
    width: 110px;
    border-radius: 30px;
    background-color: #4912c1;
    font-weight: 800;
    margin-left: 240px;
    cursor: pointer;
    color: white;
    
}

.shareImgContainer{
    padding: 0 20px 10px;
    position: relative;

}

.shareImg{
    width: 100%;
    object-fit: cover;

}

.shareCancel{
    position: absolute;
    top: 0;
    right: 20px;
    cursor: pointer;
    opacity: 0.7;
}
.rightbar{
    flex: 2.5 1;
}
  
.rightbarWrapper {
    padding: 20px 20px 0 0;
}
  
.birthdayContainer {
    display: flex;
    align-items: center;
}
  
.birthdayImg {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.birthdayText {
    font-weight: 300;
    font-size: 15px;
  }
  
.rightbarAd {
    width: 100%;
    border-radius: 10px;
    margin: 30px 0;
}

.rightbarFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.rightbarTitle{
    margin-bottom: 20px;
    font-size: 21px;
    font-weight: 500;
}

.rightbarInfo{
    margin-bottom: 30px;
}

.rightbarInfoItem{
    margin-bottom: 10px;
}

.rightbarInfoKey{
    font-weight: 500;
    margin-right: 15px;
    color: #555;
}

.rightbarInfoValue{
    font-weight: 300;
}

.rightbarFollowings{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    flex-direction: row;
}
  
.rightbarFollowing{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}

.rightbarFollowingImg{
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
  }
.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.rightbarProfileImgContainer{
    margin-right: 10px;
    position: relative;
}

.rightbarProfileImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

.rightbarOnline{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color:limegreen;
    position: absolute;
    top: -3px;
    right: 0;
    border: 2px solid white;
}

.rightbarUsername{
    font-weight: 500;
    font-size: 18px;

}
.homeContainer{
    display: flex;
    width: 100%;
}
.profile{
    display: flex;
}

.profileRight{
    flex: 10 1;
}

.profileCover{
    height: 320px;
    position: relative;
}

.profileCoverImg{
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileUserImg{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 150px;
    border: 3px solid white;
}

.profileInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profileInfoName{
    font-size: 28px;
}

.profileInfoDesc{
    font-weight: 300;
}

.profileRightBottom{
    display: flex;
}
.login {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}  

.loginWrapper {
    width: 70%;
    height: 70%;
    display: flex;
}

.loginLeft,.loginRight {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.loginLogo {
    font-size: 50px;
    font-weight: 800;
    color: #4912c1;
    margin-bottom: 10px;
}

.loginDesc {
    font-size: 25px;
}

.loginBox{
    height: 300px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 10px;
}

.loginInput{
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}

.loginInput:focus{
    outline: none;
}

.loginButton{
    height: 50px;
    width: 100%;
    border-radius: 20px;
    border: none;
    background-color: #4912c1;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    align-self: center;

}

.loginForgot{
    text-align: center;
    color: #4912c1;
}

.loginRegisterButton{
    width: 60%;
    align-self: center;
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #42b72a;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}
